import React, { FC } from 'react'
import clsx from 'clsx'
import { Typography } from '../../other/Typography'
import { LinkProps } from './types'
import './Link.scss'

const Link: FC<LinkProps> = ({ text, size = 'L', onClick, className }) => {
  return (
    <button className={clsx('LinkNext', className)} onClick={onClick}>
      <Typography
        className={clsx('LinkNext-Text', `LinkNext-Text_${size}`)}
        text={text}
      />
    </button>
  )
}

export default Link
